$font-stack: 'GothamPro', sans-serif;

@font-face {
  font-family: 'GothamPro';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro.ttf);
}

@font-face {
  font-family: 'GothamPro-Light';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Light.ttf);
}

@font-face {
  font-family: 'GothamPro-Black';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Black.ttf);
}

@font-face {
  font-family: 'GothamPro-Medium';
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/GothamPro/GothamPro-Medium.ttf);
}

$font : 'GothamPro';
$fontLight : 'GothamPro-Light';
$fontBlack : 'GothamPro-Black';
$fontMedium : 'GothamPro-Medium';


.none {
  padding: 0;
  margin: 0;
}

html {
  min-width: 1240px !important;
}

.container {
  width: 1240px !important;
}

body {
  font-family: $font-stack;
  min-width: 1240px !important;
  color: #383838;
}

.menuBlock {
  width: 677px;
  min-height: 350px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow: hidden;
  .backgroundCircle {
      background-color: #c8ae76;
      width: 25px;
      height: 25px;
      border-radius: 2500px;
      left: 258px;
      position: absolute;
      top: 33px;
      display: none;
  }
  ul {
    display: none;
    position: absolute;
    letter-spacing: 1px;
    z-index: 9999;
    right: 84px;
    top: 194px;
    list-style: none;
    color: #fff;
    font-size: 14px;
    font-family: $fontMedium;
    margin: 0;
    padding: 0;
    width: 264px;
    li {
      margin-bottom: 10px;
      a {
        color: #fff;
        &:hover {
          color: #fff;
        }
      }
    }
    #separator {
      background-color: #e1e1e1;
      height: 1px;
      width: 100%;
      margin-top: 55px;
      margin-bottom: 24px;
    }
  }
}

header {
  position: relative;
  #menuHumburger {
    width:25px;
    height:25px;
    background-image:url(../img/sprite_x25.png);
    background-position:0% 0%;
    cursor: pointer;
    display: inline-block;
    position: absolute;
    top: 33px;
    left: 203px;
    z-index: 9999;
  }
  h1 {
    position: absolute;
    top: 31px;
    right: 413px;
    letter-spacing: 1.2px;
    font-size: 26px;
    font-family: $fontLight;
  }

  h2 {
    position: absolute;
    top: 301px;
    right: 293px;
    letter-spacing: 5px;
    font-size: 42px;
    text-transform: uppercase;
    span {
      display: block;
      &:first-child {
        font-family: $fontLight;
      }
      &:last-child {
        font-family: $font;
        letter-spacing: 6.5px;
      }
    }
  }
  img {
    position: absolute;
    top: 0;
    left: 65px;
    width: 555px;
    z-index: -9999;
  }
}

.button {
  position: absolute;
    top: 456px;
    right: 296px;
    letter-spacing: 1.5px;
    font-size: 10px;
    color: #000;
    border: 1px solid #000;
    text-transform: uppercase;
    padding: 11px 53px 11px 55px;
    &:hover, &:active {
      color: #000;
    }
}
